
































































































































































import { Vue, Component } from "vue-property-decorator";

@Component
export default class AccountForm extends Vue {
  private formTitle: string = "新增帳號";
  private activeForm: Boolean = false;
  private email: string = "";
  private userPassword: string = "";
  private name: string = "";
  private introduction: string = "";
  private imageUrl: string = "";
  private buttionAction: string = "add";
  private permissionText: string = "";
  private permission: Object = ["醫生", "護理師", "管理員"];
  private valid: Boolean = true;
  private overlay: Boolean = false;
  created() {
    if (this.$route.query.action == "show") {
      this.formTitle = "顯示帳號資料";
      this.getShowData();
      this.activeForm = true;
      this.buttionAction = "close";
    } else if (this.$route.query.action == "edit") {
      this.formTitle = "更新帳號權限";
      this.buttionAction = "edit";
      this.getShowData();
    }
  }
  getShowData() {
    this.overlay = true;
    var order = "/user/" + this.$route.query.id;
    this.axios
      .get(order)
      .then(data => data.data)
      .then(({ user }) => {
        this.email = user.email;
        this.userPassword = "noshowpassword";
        if (user.role == "Admin") this.permissionText = "管理員";
        if (user.role == "Doctor") this.permissionText = "醫生";
        if (user.role == "Nurse") this.permissionText = "護理師";
        this.name = user.name;
        this.introduction =
          user.introduction === null || user.introduction === ""
            ? "無帳號描述"
            : user.introduction;
        this.imageUrl =
          user.image === null || user.image === "" ? "無照片" : user.image;
        this.overlay = false;
      })
      .catch(data => {
        this.$toasted.show(`資料讀取失敗，請重新整理一次`, {
          type: "error",
          position: "top-right",
          duration: 3000
        });
      });
  }
  close() {
    this.$router.push({
      path: "/admin/account/accountall"
    });
  }
  submit(): void {
    if ((this.$refs.form as Vue & { validate: () => boolean }).validate()) {
      this.overlay = true;
      var role;
      if (this.permissionText == "管理員") role = "Admin";
      else if (this.permissionText == "醫生") role = "Doctor";
      else role = "Nurse";
      this.axios
        .post(
          "/user",
          JSON.stringify({
            email: this.email,
            password: this.userPassword,
            name: this.name,
            role: role,
            image: this.imageUrl,
            introduction: this.introduction
          })
        )
        .then(data => data.data)
        .then(({ ok }) => {
          this.$toasted.show(`新增成功`, {
            type: "success",
            position: "top-right",
            duration: 3000
          });
          this.overlay = false;
          this.$router.push({
            path: "/admin/account/accountall"
          });
        })
        .catch(data => {
          this.overlay = false;
          this.$toasted.show(`新增失敗`, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    }
  }

  edit() {
    this.overlay = true;
    var order = "/user/" + this.$route.query.id;
    var role;
    if (this.permissionText == "管理員") role = "Admin";
    else if (this.permissionText == "醫生") role = "Doctor";
    else role = "Nurse";
    this.axios
      .post(
        order,
        JSON.stringify({
          role: role
        })
      )
      .then(data => data.data)
      .then(({ ok }) => {
        this.$toasted.show(`更新成功`, {
          type: "success",
          position: "top-right",
          duration: 3000
        });
        this.overlay = false;
        this.$router.push({
          path: "/admin/account/accountall"
        });
      })
      .catch(data => {
        this.overlay = false;
        this.$toasted.show(`新增失敗`, {
          type: "error",
          position: "top-right",
          duration: 3000
        });
      });
  }
  clear() {
    this.email = "";
    this.name = "";
    this.imageUrl = "";
    this.permissionText = "";
    this.introduction = "";
  }
}
